import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-calculator',
  templateUrl: './calculator.component.html',
  styleUrls: ['./calculator.component.scss']
})
export class CalculatorComponent implements OnInit {

  constructor() { }

  display: string = '';
  // operators:string = '';
  symbot: string = '';
  ans = "Ans =";
  numbers = [
    { id: 1 },
    { id: 2 },
    { id: 3 },
    { id: 4 },
    { id: 5 },
    { id: 6 },
    { id: 7 },
    { id: 8 },
    { id: 9 },
    { id: 0 },
    { id: "C" },
  ];

  operators = [
    { symbol: '+', name: 'add' },
    { symbol: '-', name: 'sub' },
    { symbol: '*', name: 'Mult' },
    { symbol: '/', name: 'Divis' },
    { symbol: '=', name: 'equal' },
  ];


  ngOnInit(): void {
  }




  updateDisplay(number) {
    if (number === 'C') {
      this.display = "";
    } else {
      if (this.display.length < 10) {
        this.display = this.display + number
      }
    }

  }




  // updateOperator(symbol) {
  //   if (symbol === '=') {
  //     this.calculateValue();
  //     return;
  //   }
  //   if (this.display.length > 0 ) {
  //     const sy = symbol[symbol.length-1];
  //     console.log(sy);
  //     this.display += symbol;
  //   }

  // }
  updateOperator(symbol: string) {
    if (symbol === '=') {
      this.calculateValue();
      return;
    }

    if (this.display.length === 0) return;

    const lastChar = this.display[this.display.length - 1];

    if (['+', '-', '*', '/'].includes(lastChar)) {
      this.display = this.display.slice(0, -1) + symbol;
    }
    else {
      this.display += symbol;
    }
  }



  calculateValue() {


    const result = Function('return (' + this.display + ')')();
    this.display = result.toString();

  }
}


